import React, { Component } from 'react';
import './styles.css';
import CircleButton from '../circle-button/Circle-Button';
import DetailCard from '../detail-card/Detail-Card';
import ContactIcon from '../contact-icon/Contact-Icon';
import ResumeCard from '../resume-card/Resume-Card';
import { projectData } from '../../data';

class PageSection extends Component {

    render() {
        if (this.props.section === "top") {
            return this.renderTopSection();
        } else if (this.props.section === "about") {
            return this.renderAboutSection();
        } else if (this.props.section === "resume") {
            return this.renderResumeSection();
        } else if (this.props.section === "projects") {
            return this.renderProjectSection();
        } else {
            return null;
        }
    }

    renderTopSection() {
        return (
            <div id="top">
                <h1>sarah irons</h1>
                <div className="circles">
                    {/* <CircleButton desc="about"/> */}
                    <CircleButton desc="contact"/>
                    <CircleButton desc="resume"/>
                    <CircleButton desc="projects"/>
                </div>
            </div>
        );
    }

    renderAboutSection() {
        return (
            <div id="about">
                <h3>connect with me here:</h3>
                <div className="icons" id="contact">
                    <ContactIcon contactUrl="https://www.linkedin.com/in/sarahlikesiron/" contactMethod="linkedin" contactInfo="sarahlikesiron" icon="Linkedin" />
                    <ContactIcon contactUrl="https://github.com/sarahlikesiron" contactMethod="github" contactInfo="sarahlikesiron" icon="Github" />
                    <ContactIcon contactUrl="mailto:sli8695@rit.edu" contactMethod="email" contactInfo="sli8695(at)rit.edu" icon="Email" />
                </div>
            </div>
        );
    }

    renderResumeSection() {
        return (

            <div id="resumes">
                <ResumeCard link="" image="resume_block.png" descText="(in progress)" />
                <ResumeCard link="./irons_resume_2-22.pdf" image="resume_block.png" descText="downloadable resume" />
            </div>

        );
    }

    renderProjectSection() {
        return (
            // eventually will need to add a loop for all of the projects
            // add methods for choosing which option to view
            <div id="projectsSection">
                <h2>what have I done so far?</h2>
                {/* <ul id="ddpList">
                    <li id="designOption">design</li>
                    <li id="developmentOption">development</li>
                    <li id="programmingOption">programming</li>
                </ul>
                <div className='design'>
                    {this.renderDesignCards()}
                </div>
                <div className="development">

                </div>
                <div className="programming">

                </div> fix later */}
                <div className='design'>
                    { projectData.map(project => {
                        return <DetailCard project={project} key={project.id} />
                    }) }
                </div>
            </div>
        );
    }

    renderDesignCards() {
        return <h2>design cards</h2>
    }

}

export default PageSection;